import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'
import * as R from 'ramda'
import Services from 'gatsby-theme-weknowinc/src/blocks/Services'
import Stats from 'gatsby-theme-weknowinc/src/blocks/Stats'
import FeatureText from 'gatsby-theme-weknowinc/src/blocks/FeatureText'
import MapCallout from 'gatsby-theme-weknowinc/src/blocks/MapCallout'
import Partners from 'gatsby-theme-weknowinc/src/blocks/Partners'
import BannerCallout from 'gatsby-theme-weknowinc/src/blocks/BannerCallout'
import BlogSlider from 'gatsby-theme-weknowinc/src/blocks/BlogSlider'
import HeroBackground from 'gatsby-theme-weknowinc/src/blocks/HeroBackground'
import {
  Title,
  TitleAccentLine,
} from 'gatsby-theme-weknowinc/src/components/Title'
import Divider from 'gatsby-theme-weknowinc/src/components/Divider'
import TitleMixer from 'gatsby-theme-weknowinc/src/components/TitleMixer'

import Layout from '../components/layout'
import Content from '../components/contentProcessor'

const Home = ({ data }) => {
  const { home, posts } = data
  const heroData = R.find(R.propEq('field_section_id', 'services'))(
    home.relationships.field_components
  )
  const wordMixerTags = heroData.relationships.field_hero_list.map(
    (i) => i.field_title.value
  )

  const servicesData = R.find(R.propEq('__typename', 'paragraph__grid_card'))(
    home.relationships.field_components
  ).relationships.field_card

  const statsData = R.find(R.propEq('__typename', 'paragraph__stats'))(
    home.relationships.field_components
  ).relationships.field_stat_item

  const partnersData = R.find(R.propEq('__typename', 'paragraph__partners'))(
    home.relationships.field_components
  ).relationships.field_partner_paragraph

  const featuredText = R.find(
    R.propEq('__typename', 'paragraph__50_50_two_column_content_')
  )(home.relationships.field_components).relationships

  const mapData = R.find(R.propEq('field_section_id', 'map'))(
    home.relationships.field_components
  )

  const bannerData = R.find(R.propEq('field_section_id', 'banner'))(
    home.relationships.field_components
  )
  const blogCard = R.find(R.propEq('__typename', 'paragraph__card'))(
    home.relationships.field_components
  )
  return (
    <Layout title={home.title}>
      <HeroBackground variant="home">
        <TitleMixer
          title={heroData.field_title.processed}
          words={wordMixerTags}
        />
      </HeroBackground>

      <Services
        services={servicesData.map((service) => ({
          icon:
            service.relationships.field_grid_image.relationships
              .field_media_image.localFile &&
            service.relationships.field_grid_image.relationships
              .field_media_image.localFile.childImageSharp.fluid.src,
          title: service.field_text_title,
          subtitle: service.field_subtitle,
          content: (
            <Content
              content={service.field_body.processed}
              trimMargins={true}
            />
          ),
          cta: {
            text: service.field_footer.processed,
            url:
              !R.isEmpty(service.field_link) &&
              service.field_link.shift().alias,
          },
        }))}
      />

      <Divider variant="hidden" />

      <Divider variant="hidden" />

      <Stats
        stats={statsData.map((stat) => ({
          number: stat.field_stat_value,
          text: stat.field_text_title,
        }))}
      />

      <Divider />
      <Divider variant="hidden" />

      <FeatureText contentAs={Content}>
        <Title variant="big">
          {featuredText.field_left_content_50_[0].field_title.value}
          <TitleAccentLine>
            {featuredText.field_left_content_50_[1].field_title.value}
          </TitleAccentLine>
          {featuredText.field_left_content_50_[2].field_title.value}
        </Title>
        <Content
          content={
            featuredText.field_right_content_50_[0].field_text_block.processed
          }
        />
      </FeatureText>

      <Divider variant="hidden" />
      <Divider variant="hidden" />

      <MapCallout
        image={
          mapData.relationships.field_background_image.relationships
            .field_media_image.localFile.childImageSharp.fluid.src
        }
        title={mapData.field_title.processed}
        titleAccent={mapData.field_subtitle}
        content={mapData.field_text.processed}
        cta={{
          text: mapData.relationships.field_hero_cta.field_text.value,
          url: mapData.relationships.field_hero_cta.field_cta_link.alias,
        }}
        showDots
      />

      <Divider variant="hidden" />

      <Partners
        partners={partnersData.map((partner) => ({
          image:
            partner.relationships.field_logo &&
            partner.relationships.field_logo.relationships.field_media_image
              .localFile &&
            partner.relationships.field_logo.relationships.field_media_image
              .localFile.childImageSharp.fluid.src,
          name: partner.field_partner,
          url: partner.field_website.uri,
        }))}
      />

      <Divider variant="hidden" />
      <Divider variant="hidden" />

      <BannerCallout
        image={
          bannerData.relationships.field_background_image.relationships
            .field_media_image.localFile.childImageSharp.fluid.src
        }
        title={bannerData.field_title.processed}
        content={bannerData.field_text.processed}
        cta={{
          text: bannerData.relationships.field_hero_cta.field_text.value,
          url: bannerData.relationships.field_hero_cta.field_cta_link.alias,
        }}
      />

      <Divider variant="hidden" />
      <Divider variant="hidden" />

      <BlogSlider
        title={blogCard.field_text_title}
        titleAccent={blogCard.field_subtitle}
        intro={<Content content={blogCard.field_body.processed} />}
        cta={{
          text: blogCard.field_footer.processed,
          url: blogCard.field_link[0].alias,
        }}
        posts={posts.edges.map(({ node }) => ({
          id: node.id,
          image:
            node.relationships.field_preview.relationships.field_media_image
              .localFile &&
            node.relationships.field_preview.relationships.field_media_image
              .localFile.childImageSharp.fluid.src,
          tags: ['gatsby', 'js'],
          meta: node.created,
          title: node.title,
          excerpt: node.field_resume.slice(0, 60),
          url: node.path.alias,
        }))}
      />
    </Layout>
  )
}

Home.propTypes = {
  data: PropTypes.shape({
    home: PropTypes.object.isRequired,
    posts: PropTypes.shape({
      edges: PropTypes.array,
    }).isRequired,
  }),
}

export default Home

export const homePageQuery = graphql`
  query {
    home: nodeLanding(path: { alias: { eq: "/homepage" } }) {
      path {
        alias
      }
      title
      ...PageMetaFragment
      relationships {
        field_components {
          ...ParagraphHeroFragment
          ...ParagraphJSXComponentFragment
          ...ParagraphGridCardFragment
          ...ParagraphTextContentFragment
          ...ParagraphPartnersFragment
          ...ParagraphCtaFragment
          ...ParagraphTextListFragment
          ...ParagraphStatsFragment
          ...Paragraph50x50TwoColumn
          ...ParagraphCardFragment
        }
      }
    }
    posts: allNodeBlogPost(sort: { fields: created, order: DESC }, limit: 12) {
      edges {
        node {
          id
          title
          path {
            alias
          }
          created(formatString: "MMM D, YYYY")
          fields {
            markdownBody {
              childMarkdownRemark {
                html
                rawMarkdownBody
                excerpt
              }
            }
          }
          field_resume
          relationships {
            uid {
              field_full_name
            }
            field_image {
              relationships {
                field_media_image {
                  localFile {
                    childImageSharp {
                      fluid {
                        ...GatsbyImageSharpFluid
                      }
                    }
                  }
                }
              }
            }
            field_preview {
              relationships {
                field_media_image {
                  localFile {
                    childImageSharp {
                      fluid {
                        ...GatsbyImageSharpFluid
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`
